<template>
  <div class="small-box">
    <div v-if="title ||url">
      <header class="header">
        <a v-bind:href="url">{{title}}</a>
      </header>
      <hr>
    </div>
    <slot name="main"></slot>
  </div>
</template>

<script>
export default {
  props: ["title", "url"]
};
</script>

<style scoped>
.small-box {
  border: solid 1px gray;
  margin: 5px;
}
.header {
  font-size: 200%;
}
</style>


